@use "sass:color";

* {
  box-sizing: border-box;
}

body {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  overflow: hidden;
  height: 100vh;
  margin: 0;
  padding: 0;
}

#app {
  height: 100%;
  width: 100%;

  canvas {
    outline: none;
  }
}

#audio-btn {
  align-items: center;
  color: white;
  background-color: #441F41;
  border: none;
  display: inline-flex;
  justify-content: center;
  position: absolute;
  left: 50%;
  top: 60%;
  transform: translate(-50%);
  padding: 8px;
  border-radius: 9999px;
  cursor: pointer;
  width: 240px;
  overflow: hidden;
  transition: 0.35s background-color;

  @media (min-width: 800px) {
    top: 70%;
  }

  svg {
    flex: 0 0 50px;
    transition: 0.35s transform;
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 1;
  }

  &:hover {
    background-color: color.adjust(#441F41, $lightness: 5%);

    svg {
      transform: scale(1.2);
    }
  }

  &:active {

    svg {
      transform: scale(0.9);
    }
  }
}

/* Play button turns green when user uploaded tracks */
#audio-btn.ready {
  background-color: #28a745;
}
#audio-btn.ready:hover {
  background-color: color.adjust(#28a745, $lightness: -5%);
}

#audio-btn-label {
  --clip: 0;

  flex: 0 1 120px;
  font-weight: 700;
  text-transform: uppercase;
  position: relative;
  z-index: 1;

  clip-path: polygon(
    calc(var(--clip) * 100%) 0%,
    100% 0%,
    100% 100%,
    calc(var(--clip) * 100%) 100%
  );
}

#audio-btn-loader {
  --scale: 0;

  position: absolute;
  display: block;
  background-color: #FBC40D;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform-origin: center bottom;
  transform: scaleY(var(--scale));
}

.tp-dfwv {
  width: 350px !important;
}

#credits {
  color: white;
  position: absolute;
  font-weight: 500;
  left: 0;
  line-height: 1.7;
  bottom: 30px;
  padding: 0 40px;
  width: min(100vw, 600px);

  a {
    color: #FBC40D;
  }
}

/* Uploader panels */
#uploader,
#visual-uploader {
  position: absolute;
  left: 16px;
  z-index: 5;
  color: white;
  font-size: 12px;
  background: rgba(0, 0, 0, 0.28);
  border: 1px solid rgba(255, 255, 255, 0.12);
  backdrop-filter: blur(2px);
  border-radius: 10px;
  padding: 8px;
  width: min(300px, 36vw);
  max-height: 40vh;
  overflow: hidden;
}

#uploader {
  top: 16px;
}

#visual-uploader {
  bottom: 16px;
}

#uploader .drop-area,
#visual-uploader .drop-area {
  border: 1px dashed rgba(255, 255, 255, 0.35);
  border-radius: 8px;
  padding: 10px 12px;
  text-align: left;
  cursor: pointer;
  user-select: none;
  outline: none;
}

#track-drop.is-hidden {
  display: none;
}

#uploader .drop-area:focus,
#uploader .drop-area.dragover,
#visual-uploader .drop-area:focus,
#visual-uploader .drop-area.dragover {
  border-color: #FBC40D;
  background: rgba(251, 196, 13, 0.08);
}

#uploader .drop-area .hint,
#visual-uploader .drop-area .hint {
  display: inline-block;
  opacity: 0.8;
  margin-left: 6px;
}

#visual-uploader .drop-area .drop-summary {
  margin-top: 6px;
  max-height: 96px;
  overflow-y: auto;
  font-size: 11px;
  padding-right: 2px;
}

#visual-uploader .drop-area .drop-summary ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

#visual-uploader .drop-area .drop-summary li {
  padding: 2px 0;
  opacity: 0.85;
  word-break: break-word;
}

#visual-uploader .drop-area button {
  margin-top: 8px;
  width: 100%;
  background: rgba(255, 255, 255, 0.18);
  color: white;
  border: none;
  border-radius: 6px;
  padding: 6px 10px;
  font-size: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

#visual-uploader .drop-area button:hover:enabled {
  background-color: rgba(251, 196, 13, 0.35);
}

#visual-uploader .drop-area button:disabled {
  opacity: 0.45;
  cursor: default;
}

#track-list,
#media-list {
  list-style: none;
  padding: 6px 0 0 0;
  margin: 6px 0 0 0;
  max-height: 26vh;
  overflow: auto;
}

#track-list li,
#media-list li {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 4px 2px;
}

#track-list .name {
  flex: 1 1 auto;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

#track-list button {
  background: rgba(255, 255, 255, 0.12);
  color: white;
  border: none;
  border-radius: 6px;
  padding: 2px 6px;
  font-size: 11px;
  cursor: pointer;
}

#track-list button:disabled {
  opacity: 0.4;
  cursor: default;
}

#media-list li.placeholder {
  opacity: 0.7;
  font-style: italic;
}

#media-list li.active {
  color: #FBC40D;
}

#media-list li.pending {
  color: #9ddcff;
}
